import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController, ViewController, LoadingController, AlertController, Searchbar, ToastController } from 'ionic-angular';
import { SoapService } from '../soap.service';
 
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code } from '../../config';
/**
 * Generated class for the IpcContactQuestionListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ipc-contact-question-list',
  providers: [SoapService],
  templateUrl: 'ipc-contact-question-list.html',
})
export class IpcContactQuestionListPage {
  userdataTPK:any; 
  loading:any;
  ipcContactQuestionList:Array<any> = []; 
  batasAtas = 1;
  batasBawah = 20;
  isLoading:boolean = true;  
  userdataIPCContact:any;
 
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public popoverCtrl: PopoverController,    
    public viewCtrl: ViewController, 
    public soapService:SoapService,
    public loadingCtrl:LoadingController,
    public storage:Storage,
    public alertCtrl:AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController
  ) {
    
  }

  // ionViewWillEnter() {
  //   this.storage.get('userdata').then((val) => {
  //     this.userdata = val;
  //     this.loadingPresent();      
  //     this.getDataQuestionIpcc('first','');
  //   });    
  // }

  ionViewWillEnter() {
    this.ipcContactQuestionList = [];
    this.storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val; 
    });
    this.storage.get('userdataIPCContact').then((val) => {
      this.userdataIPCContact = val;
      // console.log(this.userdataIPCContact);
      // this.loadingPresent();
      this.getDataQuestionIpcc('first','');
    });
  }

  getDataQuestionIpcc(type, functionName) {
    if (type == 'first' && functionName == '') {
      this.isLoading = true;  
    }      
    this.soapService
    .post(api_base_url,'eoffice_masalah_list',{fStream:JSON.stringify(
    //.post(api_base_url,'eoffice_data_masalahdir_ipcc',{fStream:JSON.stringify( 
      {
          usernameEDI : api_user, 
          passwordEDI : api_pass,    
          nipp : this.userdataTPK['data']['NIPP'], 
          atas : this.batasAtas,
          bawah : this.batasBawah            
            
        //nipp: this.userdata['data']['NIPP']
        //id_dir: this.userdataIPCContact['ID_DIR']
      }
    )}).then(result => {
      result = String(result).replace(/\n/g, " ");
      var responData = JSON.parse(String(result));
            // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {    
        if (type == 'refresh' && functionName != '') {
          this.ipcContactQuestionList = [];
        }    
        if (responData['data'].length > 0) {
          for (var i = 0; i < responData['data'].length; i++) {
            this.ipcContactQuestionList.push(responData['data'][i]);
          }
        }
        // console.log(this.ipcContactQuestionList);        
      } else {
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present(); 
      } 
      
      if (type == 'first' && functionName == '' ) {
        // this.loadingDismiss();
      } else if (type == 'infinite' && functionName != '') {
        functionName.complete();
      } else if (type == 'refresh' && functionName != '') {
        functionName.complete();
      }
      this.isLoading = false;  
    })
    .catch(error => {
      // console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
       
      
      this.isLoading = false;
      if (type == 'first' && functionName == '' ) {
        // this.loadingDismiss();
        toast.present();    
      } else if (type == 'infinite' && functionName != '') {
        functionName.complete();
      } else if (type == 'refresh' && functionName != '') {
        functionName.complete();
        toast.present();    
      }
    });
  }

  goToDetail(data) {
    this.navCtrl.push('IpcContactDetailPage',{
      data : data
    });
  }

  loadingPresent() {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
  }

  loadingDismiss() {
    if(this.loading.present()) {
      this.loading.dismiss(); 
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IpcContactQuestionListPage');
  }

  doInfinite(infiniteScroll) {
    if (this.ipcContactQuestionList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 20;
      this.getDataQuestionIpcc('infinite', infiniteScroll);  
    } else {
      infiniteScroll.complete();
    }
    
  }

  openModal(page) { 
    let modal = this.modalCtrl.create(page,{}, {
      enableBackdropDismiss: true,
      showBackdrop:true,
      // cssClass:'my-modal2' 
    });
    modal.present();
  }

  doRefresh(refresher) {
    this.batasAtas = 1;
    this.batasBawah = 20;

    this.getDataQuestionIpcc('refresh',refresher);
  }

  isEmptyObject(obj) {
    for(var prop in obj) {
       if (obj.hasOwnProperty(prop)) {
          return false;
       }
    }  
    return true;
  }

}



